<template>

    <!--打电话弹窗-->
    <van-popup v-model="isShowPhoneSelect" position="bottom" class="vanPopup" @close="onCancel">
        <div class="phoneSelectBox" v-if="serviceInfo">

            <div v-if="serviceInfo.onlineService==1" class="phoneSelect" @click="callOnlineService">
                <div>在线客服</div>
            </div>
            <div v-if="serviceInfo.shopTel" class="phoneSelect" @click="callProperty(serviceInfo.shopTel)">
                <div>商家电话：</div>
                <div class="phoneNum">{{serviceInfo.shopTel}}</div>
            </div>
            <div v-if="serviceInfo.serviceTel" class="phoneSelect" @click="callProperty(serviceInfo.serviceTel)">
                <div>平台客服：</div>
                <div class="phoneNum">{{serviceInfo.serviceTel}}</div>
            </div>
        </div>
        <div class="cancelPhoneBtn" @click="onCancel">取消</div>
    </van-popup>


</template>

<script>
    import { exchangeIdService } from "../../services/goodsService";
    import Config from "../../common/config";
    import { Dialog } from 'vant';

    export default {
        name: "orderItem",
        props: {
            serviceInfo: {
                type: Object,
                default: () => { }
            }
        },
        data() {
            return {
                isShowPhoneSelect: false,
                /*大字模式*/
                bindThemeL: this.$curTheme == 0 ? false : true,

            }
        },
        create() {

        },
        activated() {
            this.bindThemeL = this.$curTheme == 0 ? false : true;

        },
        watch: {
            serviceInfo(newold) {
                console.log("-----newold------")
                console.log(newold);
                if (newold.onlineService != null) {
                    if (newold.onlineService == 1 && newold.shopTel == null && newold.serviceTel == null) {
                        this.callOnlineService();
                    } else if (newold.onlineService == 0 && newold.shopTel == null && newold.serviceTel != null) {
                        window.location.href = "tel:" + newold.serviceTel;
                    } else if (newold.onlineService == 0 && newold.shopTel != null && newold.serviceTel == null) {
                        window.location.href = "tel:" + newold.shopTel;
                    } else if (newold.onlineService == 0 && newold.shopTel == null && newold.serviceTel == null) {
                        return
                    } else {
                        this.isShowPhoneSelect = true;
                    }
                }

            }
        },
        methods: {

            //取消电话弹窗
            onCancel() {
                console.log("-----1------")
                this.$emit('setServiceInfo', {});
                this.isShowPhoneSelect = false;
            },
            //打电话
            callProperty(phoneNum) {
                this.onCancel();

                setTimeout(
                    function(){
                        window.location.href = "tel:" + phoneNum;
                    }, 0);
            },

            callOnlineService() {

                // location.href = ysf('url');
                //     this.onCancel();

                console.log("this.serviceInfo");
                console.log(this.serviceInfo);
                if (this.serviceInfo.goodsInfo) {
                    var that = this;
                    ysf('config', {
                        uid: that.$userInfo.OWNER_CODE,
                        name: that.$userInfo.OWNER_NAME,
                        // email:'test@163.com',
                        mobile: that.$userInfo.PHONE,
                        // staffid:'123', // 客服id
                        // groupid: '123' // 客服组id
                        data: JSON.stringify([
                            { "type": "crm_param", "key": "spuCode", "value": that.serviceInfo.goodsInfo.spuCode },
                        ]),
                        success: function () {
                            ysf('product', {
                                show: 1, // 1为打开， 其他参数为隐藏（包括非零元素）
                                title: that.serviceInfo.goodsInfo.spuName,//this.serviceInfo.goodsInfo.spuName
                                desc: that.serviceInfo.goodsInfo.spuSalePoint,//this.serviceInfo.goodsInfo.spuSalePoint
                                picture: that.serviceInfo.goodsInfo.spuPicUrlList[0],//this.serviceInfo.goodsInfo.spuPicUrlList[0]
                                note: '',
                                tags: [{
                                    "label": "商品详情",  // 按钮名字
                                    "focusIframe": "测试", // 定位到iframe的标识，以iframe的名称匹配
                                }],
                                success: function () {// 成功回调
                                    ysf('open');
                                    that.onCancel();
                                },
                                error: function () {       // 错误回调
                                    // handle error
                                }
                            });
                        },
                        error: function () {
                            // handle error

                        }
                    });
                } else {
                    ysf('open');
                    this.onCancel();
                }

            },

        }
    }
</script>

<style scoped="scoped" lang="scss">
    .vanPopup {
        max-height: 327px;
        background: transparent;
        padding: 0 8px 8px;
        /* padding-bottom: 8px; */
        box-sizing: border-box;

        .phoneSelectBox {
            width: 345px;
            margin: 0 auto 10px;
            background: #F7F7F7;
            border-radius: 10px;

            .phoneSelect {
                height: 50px;
                font-family: PingFangSC-Regular;
                font-size: 14px;
                letter-spacing: 0;
                display: flex;
                align-items: center;
                justify-content: center;
                border-bottom: 1px solid #E5E5E5;

                div {
                    line-height: 19px;
                }

                .phoneNum {
                    color: #4A90E2;
                }
            }

            .phoneSelect:last-child {
                border-bottom: 0px;
            }
        }

        .cancelPhoneBtn {
            width: 345px;
            height: 50px;
            line-height: 50px;
            text-align: center;
            border-radius: 10px;
            background: #fff;
            margin: 0 auto;
            font-family: PingFangSC-Regular;
            font-size: 16px;
            color: #3A3A3A;
            letter-spacing: 0.0016rem;
        }
    }
</style>