<template>
  <div class="afterSaleDetail">
<!--    <van-nav-bar title="售后详情" :border="false" left-arrow @click-left="goBack"/>-->
      <navigation-bar  @onLeftClick="goBack" pageName="售后详情"></navigation-bar>
    <div class="afterSale" v-if="afterSaleDetail">
        <div class="box">
            <div class="status-box">
                <div class="status">
                    <div class="statusCenter">
                      <div class="name">{{getStatusName()}}</div>
                      <div class="dis">{{getStatusRemind()}}</div>
                    </div>
                </div>
            </div>
            <div class="good-box">
                <div class="top">
                    <div class="left">
                        <div>{{afterSaleDetail.shopName}}</div>
                        <img :src="leftIcon" alt="">
                    </div>
                    <div class="right" v-if="flag == 1 && afterSaleDetail.billStatus == 0">等待审核</div>
                    <div class="right" v-if="flag == 1 && afterSaleDetail.billStatus == 1">等待退款</div>
                    <div class="right" v-if="flag == 2 && afterSaleDetail.billStatus == 0">退款成功</div>
                    <div class="right" v-if="flag == 2 && afterSaleDetail.billStatus == 1">申请未通过</div>
                    <div class="right" v-if="flag == 3">取消申请</div>
                </div>
                <div class="good" @click="goDetail">
                    <div class="good_img">
                        <img :src="afterSaleDetail.spuPicUrl" alt="">
                    </div>
                    <div class="good_info">
                        <div class="good_name">{{afterSaleDetail.spuName}}</div>
                        <div class="good_dis">
                            <div class="good_unit">
                                <div class="unit">{{afterSaleDetail.attrValueContent}}</div>
                                <div class="num">×{{afterSaleDetail.returnCount}}</div>
                            </div>
                            <div class="good_price">
                                <span>￥</span>
                                <span>{{afterSaleDetail.returnRealTotal}}</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="order_info">
            <div class="item">
                <div class="left">售后编号</div>
                <div class="right">
                    <div class="billNo">{{afterSaleDetail.billNo}}</div>
                    <div class="copy" @click="copy(afterSaleDetail.billNo)">复制</div>
                </div>
            </div>
            <div class="item">
                <div class="left">申请时间</div>
                <div class="right">{{afterSaleDetail.createdTime}}</div>
            </div>
            <div class="item" v-if="flag == 2 && afterSaleDetail.billStatus == 0">
                <div class="left">退款方式</div>
                <div class="right">按付款时路径返还</div>
            </div>
            <div class="item" v-if="flag == 2 && afterSaleDetail.billStatus == 0">
                <div class="left">退款时间</div>
                <div class="right">{{afterSaleDetail.updatedTime}}</div>
            </div>
            <div class="item">
                <div class="left">退款原因</div>
                <div class="right">{{afterSaleDetail.returnReason}}</div>
            </div>
        </div>
        <div class="photo_box" v-if="afterSaleDetail.returnPicUrls && afterSaleDetail.returnPicUrls.length > 0">
            <div class="title">照片凭证</div>
            <div class="img_box">
                <img :src="item" alt="" v-for="(item, index) in afterSaleDetail.returnPicUrls" :key="index" @click="preview(afterSaleDetail.returnPicUrls)">
            </div>
        </div>
    </div>
    <div class="contact">
        <div class="left" @click="call">
            <img :src="phoneIcon" alt="">
            <span>客服</span>
        </div>
        <div class="right" v-if="flag == 1 && afterSaleDetail.billStatus == 0" @click="cancelApply">取消申请</div>
    </div>
    <Preview :images="images" ref="myPreview"></Preview>
    <serviceItem :serviceInfo="serviceInfo" @setServiceInfo="setServiceInfo"></serviceItem>

  </div>
</template>

<script>
import { Toast, Dialog } from "vant";
import { cancelApply } from "../../services/orderListService";
import Preview from "@/components/preview/Preview.vue";
import phoneIcon from "@/assets/images/mall/icon_customerservice_cut.png";
import leftIcon from "@/assets/images/common/home_icon_more.png";
import NavigationBar from '@/common/currency/NavigationBar.vue';
import serviceItem from "../superiorProduct/serviceItem"

export default {
  name: "afterSaleDetail",
  data() {
    return {
      phoneIcon: phoneIcon,
      leftIcon: leftIcon,
      flag: 0,
      afterSaleDetail: {},
      images: [],
      /*大字模式*/
      bindThemeL: this.$curTheme == 0 ? false : true,
      serviceInfo : null,
      showServiceItem :false,
    };
  },
  components: {
    Preview: Preview,
    serviceItem,
      NavigationBar
  },
  activated () {
      this.bindThemeL = this.$curTheme == 0 ? false : true;
    if(this.$route.params.detail) {
      this.afterSaleDetail = this.$route.params.detail;
      //  = {
      //     attrValueContent: "净含量:28ml×6片",
      //     billNo: "347273620591468544C3",
      //     billStatus: "0",
      //     contactPhone: "18561566407",
      //     createdTime: "2019-10-24 17:08:13",
      //     returnCount: 1,
      //     returnPicUrls: [],
      //     returnRealTotal: 0.16,
      //     returnReason: "退货",
      //     saleOrderBillNo: "347273221339865088C1",
      //     saleOrderSubBillNo: "347273308967264256C2",
      //     shopCode: "319019907922763776",
      //     shopName: "网易严选",
      //     skuCount: 1,
      //     skuNo: "343613708019957760",
      //     skuPrice: 89,
      //     spuCode: "343613707998986240",
      //     spuName: "二裂酵母 清透补水面膜  6片/盒 28ml×6片",
      //     spuPicUrl: "https://yanxuan-item.nosdn.127.net/2ce4616cbfd096c6878be0df37adc91b.png",
      //     updatedTime: "2019-10-24 17:18:54"
      // };
      this.flag = this.$route.params.flag;
        window.callBack_clipboard=this.callBack_clipboard;
    }
      if (this.$deciceType=='phone'){
          require('../../assets/css/phone/myOrderList/afterSaleDetail.scss')
      }else if (this.$deciceType=='portraitPad'){
          require('../../assets/css/portraitPad/myOrderList/afterSaleDetail.scss')
      }
  },
  deactivated () {
    if (this.$store.state.preview.previewId) {
      this.$store.dispatch("setPreviewId", "");
    }
  },

  methods: {
    /**
     * 页面返回事件
     */
    goBack() {
      this.$router.go(-1);
    },

    /**
     * 跳转到商品详情页面
     */
    goDetail() {
      // this.$router.push({
      //   name: "goodsDetail",
      //   params: {
      //     spuCode: this.afterSaleDetail.spuCode
      //   }
      // });
    },

    /**
     * 获取售后订单状态名称
     */
    getStatusName() {
      switch (this.flag * 1) {
        case 1:
          return this.afterSaleDetail.billStatus == 1 ? "待退款" : "待审核";
        case 2:
          return this.afterSaleDetail.billStatus == 1
            ? "申请未通过"
            : "退款成功";
        case 3:
          return "取消申请";
      }
    },

    /**
     * 获取售后订单状态提示语
     */
    getStatusRemind() {
      switch (this.flag * 1) {
        case 1:
          return "商品等待退款中···";
        case 2:
          return this.afterSaleDetail.billStatus == 1
            ? "驳回原因："+this.afterSaleDetail.coveringLetter
            : this.afterSaleDetail.updatedTime;
        case 3:
          return "申请已取消";
      }
    },

    /**
     * 取消申请售后
     */
    cancelApply() {
      cancelApply({
        billNo: this.afterSaleDetail.billNo
      }).then(
        result => {
          if (result.resultCode == 0) {
            this.$toast("申请已取消");
            this.flag = 3;
          } else {
            this.$toast(result.msg);
          }
        },
        error => {
          this.$toast(error);
        }
      );
    },

    /**
     * 拨打电话
     */
    call() {
      // const platform = navigator.userAgent;
      // const isiOS = !!platform.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
      // console.log(isiOS);
      // if (isiOS) {
      //   window.location.href = "tel:" + this.afterSaleDetail.contactPhone;
      // } else {
      //   Dialog.confirm({
      //     title: "拨打电话",
      //     message: this.afterSaleDetail.contactPhone
      //   })
      //     .then(() => {
      //       window.location.href = "tel:" + this.afterSaleDetail.contactPhone;
      //     })
      //     .catch(() => {
      //       // on cancel
      //       Dialog.close;
      //     });
      // }
      this.serviceInfo = this.afterSaleDetail.serviceInfo;
      this.showServiceItem = true;
    },
    setServiceInfo(obj){
            this.serviceInfo = obj;
        },
    /**
     * 复制订单号
     */
    // copy(billNo) {
    //   cordova.plugins.clipboard.copy(
    //     billNo,
    //     function() {
    //       Toast("复制成功");
    //     },
    //     function() {
    //       console.log("error");
    //     }
    //   );
    // },
      copy(copyNum) {
          let paramsData=copyNum;
          if (this.$devicePlatform =="ios"){
              if (window.webkit && window.webkit.messageHandlers && window.webkit.messageHandlers.JSCtrl){
                  window.webkit.messageHandlers.JSCtrl.postMessage(['request_clipboard',666,paramsData]);
              }else {
                  window.webkit.messageHandlers.naviApp.request_clipboard({
                      'pid':666,
                      'paramsData':paramsData
                  })
              }
          }else if (this.$devicePlatform == "android"){
              window.store.request_clipboard(666,paramsData);
          }
      },
      callBack_clipboard(paramsData) {
          if (paramsData=='1'){
              Toast("复制成功");
          }else {
              console.log(paramsData);
          }
      },

    /**
     * 图片预览
     * @param
     * src    Array    图片url集合
     * index  Number   当前选中图片地址
     **/
    preview(src, index) {
      this.images = src;
      this.$refs.myPreview.setIndex(index);
      this.$refs.myPreview.setIsShowPreview(true);
      this.$store.dispatch("setPreviewId", this.$refs.myPreview);
    },
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
</style>
